.chess {
    text-align: center;
  }

.test{
    display: -webkit-flex;
    display: flex;
}

.card {
  position: relative;
  height: 500px;
  width: 25%;
  background-color:rgb(225, 244, 250);
  border: solid;
  margin: auto;
}

.playerText {
  margin-top: 0px;
  background-color:dimgray;
  color: azure;
}

.pieceTaken{
  position: relative;
  width: 90%;
  height: 50%;
  margin: auto;
}

.takenText{
  position: relative;
}

.takenPiecePic{
  position: relative;
  width: 30px;
  height: 30px;
}
.SquareBox{
    height: 80px;
    width: 80px;
}
.image{
    position: relative;
    height: 60px;
    width:60px;
    -webkit-flex:1 1;
            flex:1 1;
}
/* Popup style */
.popup-box {
    position: absolute;
    background: #00000050;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    opacity: 1;
    -webkit-flex: 2 1;
            flex: 2 1;
  }
  
  .box {
    position: absolute;
    width: 37%;
    left: 30%;
    top: 40%;
    margin: 0 auto;
    height: auto;
    max-height: 70vh;
    background: white;
    border-radius: 4px;
    padding: 20px;
    border: 1px solid black;
    opacity: 1;
    -webkit-flex:2 1;
            flex:2 1;
  }
